@font-face {
  font-family: roboto;
  src: url(../src/fonts/RobotoFlex-VariableFont_GRAD\,XTRA\,YOPQ\,YTAS\,YTDE\,YTFI\,YTLC\,YTUC\,opsz\,slnt\,wdth\,wght.ttf);
}

@font-face {
  font-family: inter;
  src: url(../src/fonts/Inter-VariableFont_slnt\,wght.ttf);
}

@font-face {
  font-family: poppins;
  src: url(../src/fonts/Poppins-Medium.ttf);
}

:root {
  --main-theme-color: rgb(243, 94, 118);
}

/*Very important. If you want to change the site's theme-color variable, you also have to change it in the navbar component. Very important*/

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #000000;
  color: white;
  width: 100vw;
}

nav {
  height: fit-content;
  display: flex;
  justify-content: space-between;
  max-width: 1100px;
  margin: 0 auto;
  padding: 10px 20px;
  align-items: center;
  font-family: poppins, Arial, Helvetica, sans-serif;
  width: 100vw;
}

nav img {
  height: 40px;
}

.nav-links-cont a {
  text-decoration: none;
  margin: 0 20px;
  font-size: 20px;
  color: white;
}

.nav-links-cont a:hover {
  color: var(--main-theme-color) !important;
}

.small-menu {
  position: relative;
}

.small-menu svg {
  width: 30px;
  height: 30px;
  fill: white;
}

.small-menu-cont {
  width: fit-content;
  height: fit-content;
  position: absolute;
  top: 100%;
  background-color: white;
  right: 0;
  z-index: 100;
}

.small-menu-cont a {
  padding: 10px 30px;
  color: #000000;
  display: block;
  text-decoration: none;
}

.small-menu-cont a:hover {
  background-color: black;
  color: white;
}

#home{
  color: var(--main-theme-color);
}

.hero-cover {
  position: relative;
}

.dark-cover {
  position: absolute;
  height: 70vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, .4);
  z-index: 2;
  top: 0;
}

@media screen and (max-width: 700px) {
  .dark-cover{
    height: 50vh !important;
  }
}

.hero-words-cont {
  position: absolute;
  font-family: poppins, Arial, Helvetica, sans-serif;
  right: 100px;
  top: 50%;
  transform: translateY(-50%);
  width: fit-content;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
}

@media screen and (max-width: 550px) {
  .hero-words-cont {
    right: unset !important;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}

  .hero-words-cont h1{
    font-size: 35px !important;
  }
}

.hero-words-cont h1 {
  text-align: center;
  font-weight: 700;
  font-size: 45px;
  color: var(--main-theme-color);
  font-family: roboto, Arial, Helvetica, sans-serif;
}

.hero-words-cont p {
  font-weight: 700;
  font-size: 20px;
  text-align: center;
}

.new-section {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.new-section>h1 {
  text-align: center;
  font-size: 45px;
  font-weight: 900;
  font-family: roboto;
  margin-bottom: 3rem;
}

.cards-cont {
  margin: 0 auto;
  padding: 0 1rem;
  /*Don't uncomment this unless you wanna use grid */
  /*   display: grid;
  grid-template-columns: repeat(auto-fit, 200px);
  grid-column-gap: 1rem;
  grid-row-gap: 1rem; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Part of what to uncomment to use the in-place scroll for the games container */
/* .scroll{
  height: fit-content;
  max-height: 80vh;
  overflow-y: auto;
} */

.scroll::-webkit-scrollbar{
  display: none;
}

.card-container {
  margin: 10px;
  width: 200px;
  background-color: rgb(27, 27, 27);
  position: relative;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
}

.card-dark-cover {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 100;
  transition: all 0.2s;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -ms-transition: all 0.2s;
  -o-transition: all 0.2s;
}

.card-dark-cover:hover {
  background-color: rgba(0, 0, 0, .3);
}

.card-words-cont {
  margin-top: auto;
}

.card-container .get-the-game {
  text-decoration: none;
  color: white;
  display: block;
  margin-left: 10px;
  margin-top: 4px;
  width: fit-content;
  background-color: var(--main-theme-color);
  padding: 3px 5px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  font-family: poppins, Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: rgb(241, 228, 228);
  z-index: 10;
}

@media screen and (max-width: 720px) {
  .card-container{
    width: 150px;
  }

  .card-container img{
    height: calc(150px * 1.3) !important;
    width: 150px !important;
  }

  .card-container h1{
    font-size: 20px !important;
  }

  .card-container p{
    font-size: 12px !important;
  }

  .get-the-game{
    font-size: 12px !important;
  }
}

@media screen and (max-width: 300px) {
  .card-container{
    width: 80px !important;
  }

  .card-container img{
    height: calc(80px * 1.4) !important;
    width: 80px !important;
  }

  .card-container h1{
    font-size: 16px !important;
  }

  .card-container p, .get-the-game{
    font-size: 9px !important;
  }
}

.card-container img {
  width: 100%;
  max-width: 200px;
  height: calc(200px * 1.2);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 200px;
}

.card-container h1 {
  font-family: poppins, Arial, Helvetica, sans-serif;
  font-size: 25px;
  margin-left: 10px;
}

.card-container p {
  font-size: 14px;
  background-color: rgb(78, 76, 76);
  border-radius: 10px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  margin-left: 10px;
  width: fit-content;
  height: fit-content;
  padding: 1px 7px;
  font-family: roboto, Arial, Helvetica, sans-serif;
  color: rgb(43, 36, 36);
}

.genre-items {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 5px 0;
}

.new-game {
  display: block;
  background-color: red;
  position: absolute;
  top: 0;
  left: 0;
  font-family: inter, Arial, Helvetica, sans-serif;
  padding: 5px;
  font-size: 14px;
}

.news-section {
  background-color: white;
}

.news-section h1 {
  color: #000000;
}

.article-container {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  width: fit-content;
  padding: 2rem 1.5rem;
  border-bottom: 1px solid gray;
}

@media screen and (max-width: 810px) {
  .article-container{
    flex-direction: column;
    align-items: center;
  }

  .article-words-cont{
    margin-top: 1rem;
  }

  .article-words-cont h1{
    text-align: center;
  }

  .article-words-cont a{
    margin: 0 auto;
    margin-top: 1rem !important;
  }
}

.article-container>img {
  display: block;
  max-width: 300px;
}

.article-words-cont {
  color: black;
  max-width: 700px;
  margin-left: 2rem;
  display: flex;
  flex-direction: column;
}

.article-words-cont h1 {
  font-size: 30px;
  font-weight: 900;
  font-family: inter, Arial, Helvetica, sans-serif;
  margin-bottom: 1rem;
  text-align: left;
}

@media screen and (max-width: 500px) {
  .article-words-cont h1{
    font-size: 25px;
  }
}

.article-words-cont p {
  font-family: poppins, Arial, Helvetica, sans-serif;
}

.article-words-cont a {
  padding: 0.5rem 1rem;
  border: 1px solid var(--main-theme-color);
  background-color: white;
  margin-top: auto;
  display: block;
  width: fit-content;
  height: fit-content;
  text-decoration: none;
  color: black;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.article-words-cont a:hover {
  background-color: var(--main-theme-color);
  color: white;
}

.search-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
  width: fit-content;
  height: fit-content;
  border: 1px solid var(--main-theme-color);
  padding: 5px 7px 5px 5px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.search-container input {
  border: none;
  background-color: transparent;
  height: 20px;
  outline: none;
  color: white;
  font-family: poppins, Arial, Helvetica, sans-serif;
}

.search-container svg {
  margin-left: 5px;
  fill: var(--main-theme-color);
}

.library-container {
  display: flex;
  flex-direction: row;
  padding: 1rem 2rem;
  position: relative;
  width: 100vw;
}

@media screen and (max-width: 1090px) {
  .library-container{
    flex-direction: column;
    padding: 1rem 0.9rem !important;
  }
}

.categories-item {
  display: block;
  text-decoration: none;
  color: white;
  font-family: poppins, Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 20px;
  border: 1px solid white;
  text-align: center;
}

.categories-item:hover{
  background-color: white;
  color: #000000;
}

.games-container {
  margin-left: 2rem;
  width: fit-content;
  margin: 0 auto;
}

@media screen and (max-width: 700px) {
  .games-container{
    margin-left: 0 !important;
    width: 100vw;
  }
}

.testinging {
  font-size: 30px;
  font-weight: 900;
  color: white;
  width: 100%;
}

.testinging>h1 {
  text-align: center;
  font-size: 35px;
}

.sidebar-container{
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 1090px) {
  .sidebar-container{
    flex-direction: row;
    justify-content: space-between;
    max-width: 100%;
  }
}

.nav-container {
  box-shadow: 1px 1px 3px var(--main-theme-color);
}

.error-message {
  color: white;
  font-family: roboto, Arial, Helvetica, sans-serif;
  font-size: 30px;
  text-align: center;
  margin-top: 50px;
}

.categories-words{
  color: #000000;
  background-color: white;
  font-family: poppins, Arial, Helvetica, sans-serif;
  padding: 5px;
}

.small-categories-cont{
  margin-left: 2rem;
  margin-right: 1rem;
}

.small-categories-item{
  background-color: white;
  color: black;
  display: block;
  text-decoration: none;
  text-align: center;
  font-family: poppins, Arial, Helvetica, sans-serif;
  border: 0.2px solid gray;
  padding: 1px 10px;
}

.small-categories-item:hover{
  background-color: #000000;
  color: white;
}

@media screen and (max-width: 700px) {
  .categories-words, .small-categories-item{
    font-size: 12px;
  }

  .search-container input{
    max-width: 30vw !important;
    height: 15px !important;
  }
}

body{
  user-select: none;
  overflow-x: hidden;
}

.container{
  position: relative;
  min-height: 100vh;
  width: 100vw;
}

.footer{
  background-color: #181717;
  width: 100vw;
}

.copyright{
  text-align: center;
  font-family: poppins, Arial, Helvetica, sans-serif;
  padding: 0.5rem 1rem;
  /* background-color: #000000; */
}

.results-for{
  margin: 0 auto;
  text-align: center;
  margin-bottom: 2rem;
  font-size: 25px;
}

.white-font{
  color: white;
}

.black-font{
  color: #000000;
}

.see-more-button{
  text-decoration: none;
  background-color: transparent;
  border: 1px solid var(--main-theme-color);
  font-family: poppins, Arial, Helvetica, sans-serif;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  margin: 0 auto;
  display: block;
  width: fit-content;
  height: fit-content;
  margin-top: 1.8rem;
}

.see-more-button:hover{
  background-color: var(--main-theme-color);
}

.events-card-cont{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0 auto;
  padding: 0 2rem;
}

@media screen and (max-width: 800px) {
  .events-card-cont{
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 560px) {
  .events-card-cont{
    grid-template-columns: 1fr;
  }
}

.event-card-cont{
  max-width: 80vw;
  border: 0.5px solid var(--main-theme-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: poppins, Arial, Helvetica, sans-serif;
  padding: 0.9rem;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  margin: 10px;
}

.event-card-cont>a{
  display: block;
  margin-top: auto;
  border: 1px solid var(--main-theme-color);
  text-decoration: none;
  color: white;
  width: fit-content;
  height: fit-content;
  padding: 0.5rem 1rem;
}

.event-card-cont>p{
  margin-bottom: 1rem;
}

.event-card-cont>h3, .event-card-cont>h4{
  font-family: roboto, Arial, Helvetica, sans-serif;
}

.event-card-cont>img{
  width: 100%;
  height: 150px;
  background-color: white;
  margin-bottom: 20px;
}

.event-card-cont>h3{
  font-size: 25px;
}

.event-card-cont>h4{
  color: rgb(224, 130, 130);
  margin: 5px 0;
}

.contacts-newsletter-cont{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem 2rem;
  max-width: 1100px;
  margin: 0 auto;
}

@media screen and (max-width: 600px) {
  .contacts-newsletter-cont{
    flex-direction: column;
    align-items: center;
  }

  .contacts-cont{
    margin-bottom: 2rem;
  }
}

.contacts-newsletter-cont h2{
  font-family: roboto, Arial, Helvetica, sans-serif;
  font-size: 30px;
}

.contacts-cont{
  width: fit-content;
}

.contacts-cont h2{
  text-align: center;
}

.contacts-cont p{
  font-family: poppins, Arial, Helvetica, sans-serif;
  margin: 0.5rem 0;
}

.social-icons-cont{
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  width: fit-content;
}

.social-icon{
  margin: 2px 8px;
}

.social-icon svg{
  fill: white;
}

.social-icon svg:hover{
  fill: var(--main-theme-color);
}

.newsletter-cont{
  width: fit-content;
}

.newsletter-form input{
  height: 30px;
  width: 100%;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  outline: none;
  font-family: poppins, Arial, Helvetica, sans-serif;
  font-size: 18px;
  margin: 0.5rem 0 0.5rem;
}

.newsletter-form button{
  padding: 0.3rem 0.8rem;
  display: block;
  margin: 0 auto;
  font-family: poppins, Arial, Helvetica, sans-serif;
  border: none;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.newsletter-form button:hover{
  background-color: var(--main-theme-color);
}